/* Estilos para el formulario flotante */
.floating-form {
  position: fixed;
  bottom: 1rem;
  right: 1rem;
  z-index: 50;
  width: 100%;
  max-width: 24rem;
  background: white;
  border-radius: 0.5rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

/* Cuando el formulario está abierto, ocultar los botones flotantes */
.floating-form ~ .floating-buttons {
  display: none;
}

/* Responsive */
@media (min-width: 768px) {
  .appointment-button span,
  .whatsapp-button span {
    display: inline;
  }
}

@media (max-width: 767px) {
  .appointment-button span,
  .whatsapp-button span {
    display: none;
  }
  
  .floating-form {
    max-width: calc(100% - 2rem);
  }
}

